import orderItem from "../../components/orderItem.vue";
import { get,post } from "../../libs/request.js"
export default {
  data() {
    return {
		orderData:[]
	};
  },
  onLoad() {},
  components: {
    orderItem
  },
  onShow(){
	  this.getMyOrder();
  },
  methods:{
	  // 获取我的订单
	  getMyOrder(){
		  post('order/list',{}).then(res=>{
			  if(res.code === 200){
				  this.orderData = res.data
			  }
		  })
	  }
  }
};
